import { createSlice } from "@reduxjs/toolkit";

//user state를 관리하는 slice 생성
let userSlice = createSlice({
  name: "user", //state 이름 지정
  initialState: { name: "형미", age: 27 }, //초깃값 설정

  reducers: {
    changeName(state) {
      state.name = "소하";
      state.age = 28;
    },
    increase(state, action) {
      state.age += action.payload;
      //action.payload는 해당 액션과 같이 값을 전송함.
    },
  },
});

//user함수에 필요한 요소를 모두 가져옴.
export let { changeName, increase } = userSlice.actions;

export default userSlice;
