import "../App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { Container, Row, Col, Button, Tab, Tabs, Nav } from "react-bootstrap";
import { Navigate, useNavigate, useParams } from "react-router-dom";
import Counter from "../component/Counter";
import { useState, useEffect } from "react";
import styled, { css } from "styled-components";
import { useDispatch } from "react-redux";
import { addItem } from "../store/cartSlice";

let OrangeBtn = styled.button`
  background: orange;
  color: white;
  padding: 7px;
  border-radius: 5px;
  margin-left: 5px;
  border: none;
  position: relative;
  top: 2px;
`;

const Detail = (props) => {
  let dispatch = useDispatch(); //데이터를 내보내는 함수
  let Navigate = useNavigate();
  let [tab, setTab] = useState(0);
  let [num, setNum] = useState("");

  useEffect(() => {
    //사용자가  detail페이지에 처음으로 접속하면
    //console.log(choose.id)//현재페이지의 상품 id만 가져오기
    let takeOut = localStorage.getItem("watched"); //2.watched데이터 가져오가
    //3. localStorage에 추가
    takeOut = takeOut ? JSON.parse(takeOut) : []; //JSON데이터를 오브젝트 형으로 변환
    takeOut.push(choose.id); // 선택한 상품추가
    takeOut = new Set(takeOut); //중복 제거
    takeOut = Array.from(takeOut); //중복제거한것 다시 array 로변환
    localStorage.setItem("watched", JSON.stringify(takeOut));
  });

  useEffect(() => {
    if (isNaN(num) === true) {
      //숫자 외에 값을 입력하면 경고창 띄우기
      alert("숫자만 입력하세요.");
    }
  });

  //App.js의 goods상품의 데이터를 전달 받아야 함.
  let { id } = useParams();
  console.log(id); //콘솔에서 확인
  //.find()문법은 array 자료 안에서 원하는 항목만 찾을 수 있음.
  let choose = props.goods.find(function (a) {
    return a.id == id; //array.id == url에 입력한 번호와 같을 경우 변수 choose에 담기
  });

  return (
    <div>
      <Container>
        <Row>
          <Col>
            <input
              onChange={(e) => {
                setNum(e.target.value);
              }}
            />
          </Col>
        </Row>
        <Row>
          <Col sm={6}>
            <img
              src={
                process.env.PUBLIC_URL +
                "/img/cont3_cake" +
                Number(choose.id) +
                ".png"
              }
              width="100%"
              alt="cake1"
            />
          </Col>
          <Col sm={6}>
            <h4>{choose.title}</h4>
            <p>{choose.content}</p>
            <p>{choose.price}</p>
            <Button
              variant="danger"
              onClick={() => {
                dispatch(
                  addItem({
                    id: choose.id,
                    img: choose.img,
                    name: choose.title,
                    price: choose.price,
                    count: 1,
                  })
                );
                Navigate("/Cart");
              }}
            >
              주문하기
            </Button>
            <OrangeBtn>취소하기</OrangeBtn>
          </Col>
        </Row>
        {/* <Counter /> */}
      </Container>
      {/* 부트스트랩 탭 UI */}
      <Container>
        <Nav variant="tabs" defaultActiveKey="tab01">
          <Nav.Item>
            <Nav.Link
              eventKey="tab01"
              onClick={() => {
                setTab(0);
              }}
            >
              상품상세
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link
              eventKey="tab02"
              onClick={() => {
                setTab(1);
              }}
            >
              상품평
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link
              eventKey="tab03"
              onClick={() => {
                setTab(2);
              }}
            >
              상품문의
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link eventKey="disabled">배송/교환/반품 안내</Nav.Link>
          </Nav.Item>
        </Nav>
        <Tabcontent tab={tab} /> {/* 변수 tab을 불러와 html에서 출력 */}
      </Container>
    </div>
  );
};

function Tabcontent({ tab }) {
  let [fade, setFade] = useState("");

  useEffect(() => {
    setTimeout(() => {
      setFade("end");
    }, 500);
    return () => {
      setFade(""); //실행 후 초기화
    };
  }, [tab]); //탭이 변경 될 때마다 실행
  if (tab === 0) {
    return <div className={`start ` + fade}>상품상세</div>;
  }
  if (tab === 1) {
    return <div className={`start ` + fade}>상품명</div>;
  }
  if (tab === 2) {
    return <div className={`start ` + fade}>배송/교환/반품 안내</div>;
  }
  if (tab === 3) {
    return <div className={`start ` + fade}>상품문의</div>;
  }
}

export default Detail;
