import { combineReducers, configureStore } from "@reduxjs/toolkit";
import { persistReducer } from 'redux-persist';
import storage from 'redux-persist/lib/storage';

import userSlice from "./store/userSlice";
import cartSlice from "./store/cartSlice";

//reducer들을 결합. 리듀서는 애플리케이션의 상태(state)와 엑션(action)을 입력으로 받아 새로운 상태를 반환하는 함수
const reducers = combineReducers({
  user: userSlice.reducer,
  cart: cartSlice.reducer
});

const persistConfig = {
  key:'root',
  storage:storage,
  whitelist: ['user','cart'], //영구적으로 저장할 슬라이스 목록
  blacklist: ['stock'], //영구적으로 저장하지 않을 슬라이스 목록
}

// 결합한 combineReducer(reducer)을 persistReducer안에 넣어준다.
const persisedtReducer = persistReducer(persistConfig, reducers);

// configureStore의 reducer안에 persistReducer를 넣어준다.
const store = configureStore({
  reducer:persisedtReducer
});

export default store;


